.indexPromo {
	.section();
	
	&-grid {
		.grid();
	}
	
	&-cell {
		.cell(12);
	}
	
	&-content {
		max-width: 530px;
		
		.btn {
			margin-top: 25px;
			
			@media (@sm) {
				max-width: 255px;
			}
		}
	}
	
	&-p {
		font-size: 16px;
		line-height: 26px;
		margin-bottom: 20px;
	}
	
	&-h {
		font-weight: bold;
		font-size: 26px;
		line-height: 35px;
		margin-bottom: 25px;
	}
	
	&-img {
		.cc();
	}
	
	@media (@mdx) {
		&-img {
			margin-right: -45px;
		}
	}
	
	@media (@md) {
		&-h {
			.h1();
			margin-bottom: 40px;
		}
		
		&-cell {
			&._img {
				.cell(5);
			}
			&._content {
				.cell(7);
				// justify-content: center;
			}
		}
		
		&-p {
			font-size: 20px;
			line-height: 30px;
		}
	}
	
	@media (@lgx) {
		&-content {
			margin-top: auto;
			margin-bottom: auto;
		}
	}
	
	@media (@lg) {
		margin: -40px 0;
		
		&-content {
			position: relative;
			padding-top: 150px;
			
			&::before {
				position: absolute;
				top: 120px;
				left: -64px;
				content: '';
				width: 475px;
				height: 404px;
				background-image: svg-load('indexPromo-bg.svg');
				z-index: -1;
			}
			
			.btn {
				margin-top: 40px;
			}
		}
	}
}
